if (Meteor.isServer) {
  Meteor.publish('coll', function(name, selector, options) {
    return coll[name].find(selector, options);
  });
  Meteor.methods({
    "import": function(name, selector, modifier) {
      return coll[name].upsert(selector, {
        $set: modifier
      });
    },
    empty: function(name, selector) {
      return coll[name].remove(selector);
    },
    update: function(name, obj) {
      return coll[name].update(obj._id, {
        $set: obj
      });
    },
    tems: function() {
      var grups, items, source;
      source = coll.tem.find().fetch();
      grups = _.uniqBy(source, function(i) {
        return i.grup;
      });
      items = _.map(grups, function(i) {
        var filter;
        filter = _.filter(source, function(j) {
          return j.grup === i.grup;
        });
        return {
          grup: i.grup,
          items: _.uniq(_.map(filter, function(j) {
            return j.item;
          }))
        };
      });
      return _.filter(items, function(i) {
        return i.grup;
      });
    }
  });
}
